import './App.css';
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import DataTable from 'react-data-table-component';



function Universities() {
const column =[
{
  name:"Universites",
  selector: row => row.name,
  sortable: true
},
{
  name:"Website",
  selector: row => row.web_pages,
  sortable: true
},
{
  name:"Location",
  selector: row => row.country,
  sortable: true
},

]



useEffect(()=>{
const fetData =async()=>{
  axios.get('http://universities.hipolabs.com/search?country=India')
  .then(res =>{ 
    setRecords(res.data) 
    setfilterRecords(res.data)
  }) .catch(err => console.log(err));
}
fetData();
},[])


const [records, setRecords] = useState([]);
const [filterRecords, setfilterRecords] = useState([]);

const handleFilter = (event) => {
  const newData = filterRecords.filter(row => row.name.toLowerCase().includes(event.target.value.toLowerCase()))
  setRecords(newData);

}
  return (
    <div>
      <div><input type='text' placeholder='Search...' onChange={handleFilter}/></div>
      <DataTable
      columns={column} data={records} pagination noDataComponent="Loading..." 
      ></DataTable>
    </div>
  );
}


export default Universities;
